export const api = ({ method, args = {}, freeze=true  }) =>
    new Promise((resolve, reject) =>
        frappe.call({ method, args, freeze, callback: ({ message }) => resolve(message), error: reject }),
    );


export const getPatientAppointmentsCount = ({ practitioner, service_unit }) => {
    return api({
        method: 'clinical.api.appointment.appointment.get_patient_appointment_count',
        args: {
            practitioner,
            service_unit
        }
    })
}

export const getCurrentLoggedInUser = () => {
    return api({
        method: 'clinical.api.appointment.appointment.get_current_logged_in_user',
    })
}