import Main from "./Main.vue";
import vSelect from 'vue-select'
import DrugAdministrationPage from './../drug_administration/InpatientDrugAdministrationPage.vue'
import store from "../state/store.js";
frappe.provide("frappe.rec"); // create a namespace within the Frappe instance
frappe.provide("frappe.inpatient");
import 'vue-select/dist/vue-select.css';

frappe.rec.Chart = class {
    // create a glue class, wich will manage your Vue instance
    constructor({ parent }) {
        this.$parent = $(parent);
        this.page = parent.page;
        this.make_body();
    }

    make_body() {
        $(".sticky-top").addClass("hidden");
        this.$body = this.$parent.find(".layout-main-section");
        this.$parent.find(".page-head").addClass("hidden");
        this.$parent.find(".page-body").removeClass("container");
        this.$parent.find(".content").removeClass("page-container");
        this.$parent.find(".page-content").removeClass("page-content");
        this.$parent
            .find("layout-main-section-wrapper")
            .addClass("no-bottom-margin");
        this.$page_container = $('<div class="hub-page-container">').appendTo(
            this.$body
        );

        Vue.component('v-select', vSelect)
        
        new Vue({
            store,
            router: frappe.custom.router,
            el: ".hub-page-container",
            render: (h) => h(Main),
        });

        if(navigator.serviceWorker){
            console.log("Service workers supported")
            window.addEventListener('load', ()=> {
              navigator.serviceWorker.register('/worker.js')
              .then( reg => console.log('Service worker registered'))
              .catch(err=> console.log( ` Error occured ${err}`))
            })
        
        }else{
            console.log('SERVICE WORKER NOT SUPPORTED')
        }
    }
};


